import React from "react";
import Link from "next/link";

import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Container
} from "reactstrap";

export default class Navbarclass extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
  render() {
    return (
      <div>
        <Navbar color="light" light expand="md" className="rtl shadow" fixed>
          <Container>
            <NavbarBrand href="/">
              <img src="/static/images/logo.png" width="100" className="ml-1" />
              <span className="mr-1">چلیپا کابل پویا</span>
            </NavbarBrand>
            <NavbarToggler onClick={this.toggle} />
            <Collapse isOpen={this.state.isOpen} navbar>
              <Nav className="ml-auto" navbar>
                <NavItem>
                  <NavLink href="/components/">Components</NavLink>
                </NavItem>
                <Link>
                  <NavItem>
                    <NavLink href="/">صفحه اصلی</NavLink>
                  </NavItem>
                </Link>
                <UncontrolledDropdown nav inNavbar>
                  <DropdownToggle nav caret>
                    محصولات
                  </DropdownToggle>
                  <DropdownMenu right>
                    <Link>
                    <a>
                    <DropdownItem>Option 1</DropdownItem>
                    </a>
                    </Link>
                    <DropdownItem>Option 2</DropdownItem>
                    <DropdownItem>Reset</DropdownItem>
                  </DropdownMenu>
                </UncontrolledDropdown>
              </Nav>
            </Collapse>
          </Container>
        </Navbar>
      </div>
    );
  }
}
